import Alert from './Alert.vue' // 
const Alerter = {
  install(Vue, options) {
    Vue.component(Alert.name, Alert) // vuePayKeyboard.name name
    //  this.$xxx  
    // Vue.prototype.$xxx  //  this.$xxx 
    // $  
  }
}
if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.use(Alerter);
}
export default Alerter // ..


